.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 5px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}
.product__info{
    height: 100px;
    margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
    margin-bottom: 5px;
}
.product__star{
    width: 24px;
}
.product__rating{
    display: flex;
}
.product>img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product>button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #ab8734 #9c7e31 #846a29;
}